.calculator{z-index:5;width:40rem;height:64rem;min-height:45rem;border-radius:.7rem;overflow:hidden;line-height:1.1;transition:all .5s ease;display:flex;flex-direction:column}@media only screen and (max-width: 37.5em){.calculator{width:92%;max-height:90vh;height:70rem}}.calculator__result{overflow-x:scroll;overflow-y:hidden;flex-basis:20%;width:100%;background-color:#212121;padding:3rem;display:flex;flex-direction:column;justify-content:center}.calculator__operation{white-space:nowrap;width:100%;min-height:1.9rem;font-size:1.9rem;font-weight:300;-moz-user-select:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:end;color:#ffc3cc;margin-top:1rem}.calculator__result-value{width:100%;text-align:end;color:#FDFDFD;font-weight:300;font-size:6rem}.calculator__controls{flex:1 1;display:grid;grid-template-rows:repeat(5, 1fr);grid-template-columns:repeat(4, 1fr)}.calculator__button{display:flex;align-items:center;justify-content:center;font-size:2.8rem;font-weight:300;-moz-user-select:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.calculator__button:hover{background:#f752a4;color:#FDFDFD}.calculator__button:active{box-shadow:inset 0 0 1.5rem rgba(87,87,87,0.7);font-size:2.4rem}.calculator__button--basic{color:#212121;background:linear-gradient(128deg, #FDFDFD 0%, #f1f1f1 100%)}.calculator__button--violet-1{color:#FDFDFD;background-color:#9156E4}.calculator__button--violet-2{color:#FDFDFD;background-color:#8C51DD}.calculator__button--violet-3{color:#FDFDFD;background-color:#894EDA}.calculator__button--violet-4{color:#FDFDFD;background-color:#8547D8}.calculator__button--pink{grid-column:3 / span 2;color:#FDFDFD;background-color:#E62F89}.negation{display:flex}.negation__up{transform:translate(-0.2rem, -0.4rem);font-size:2.4rem}.negation__down{transform:translate(0.2rem, 0.4rem);font-size:2.4rem}*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;font-weight:400}@media only screen and (min-width: 112.5em){html{font-size:75%}}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}body{box-sizing:border-box;font-family:'Source Sans Pro', sans-serif, monospace, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}::selection{background-color:rgba(139,83,218,0.6);color:#FDFDFD}.app{position:relative;width:100%;min-height:100vh;height:100%;background:#9d5dd7;background:linear-gradient(128deg, #9d5dd7 0%, #f59074 100%);display:flex;justify-content:center;align-items:center}.app__caption{font-style:italic;position:fixed;bottom:1rem;left:50%;transform:translateX(-50%);font-size:1.6rem;color:#FDFDFD;font-weight:300}.link{color:inherit;text-decoration:none;transition:all .2s ease}.link:hover{color:#E62F89}::-webkit-scrollbar{background-color:transparent;height:1rem}@media only screen and (max-width: 37.5em){::-webkit-scrollbar{height:4px}}::-webkit-scrollbar-track{background:transparent}::-webkit-scrollbar-thumb{background:#ffc3cc}::-webkit-scrollbar-thumb:hover{background:#f752a4}

